<template>
    <div class="header-notice" ref="notice" @click="$router.push({name:'noticelist'})">
        <ul class="ul">
            <li v-for="(item,index) in list" :key="index">{{item.title}}</li>
        </ul>
    </div>
</template>

<script>
    import { queryhomenotice } from "@/request/api";
    export default {
        name: 'headerNotice',
        data () {
            return {
                list:[],
                time:null,
                area:null,
                init:false
            };
        },
        created () {
            this._query()
        },
        mounted () {
            this.$nextTick(()=>{
                this.area = this.$refs.notice
                this.area.scrollTop=0;
                setTimeout(this.startScroll,3000)
            })
        },
        methods: {
            _query(){
                queryhomenotice().then(res=>{
                    let { msg, code, result } = res;
                    if(code == 200){
                        this.list = result.list
                    }
                })
            },
            startScroll(){
                if(!this.init){
                    this.area.innerHTML+=this.area.innerHTML;//克隆一份一样的内容
                }
                this.area.scrollTop++;
                this.time=setInterval(this.scrollUp,50);
                this.init = true;
            },
            scrollUp(){
                if(this.area.scrollTop % 40==0)
                {
                    clearInterval(this.time);
                    setTimeout(this.startScroll,3000);
                }else{
                    this.area.scrollTop++;
                    if(this.area.scrollTop >= this.area.scrollHeight/2){
                        this.area.scrollTop =0;
                    }
                }
            }
        }

    };
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    .header-notice{
        width: 100vw;
        overflow: hidden;
        height: 40px;
        line-height: 40px;
        background-color: #fff;
        color: #f4733b;
        white-space:nowrap;
    }
    .ul li{
        width: 100%;
        padding: 0 10px;
        box-sizing: border-box;
        text-overflow: ellipsis;
        overflow: hidden;
    }
</style>



