                            </li>
                        </ul>
                    </div>
                    <div class="cd-box">
                        单雷吃鸡无彩蛋，请先选择吃鸡类型
                    </div>
                </div>

                <div class="items" v-if="typeAct ==3">
                    <div class="title">吃鸡多雷场（取微信订单号后5位）</div>
                    <div class="list">
                        <ul>
                            <li>
                                <div class="left-title">吃鸡下注</div>
                            </li>
                            <li class="li-choose">
                                <div class="left-title">1-9任意5位数字</div>
                            </li>

                            <li class="li-title">
                                <div class="left-box">扫雷下注</div>
                                <div class="mid-box">匹配结果</div>
                                <div class="right-box">赔率</div>
                            </li>

                            <li>
                                <div class="left-box">2位数</div>
                                <div class="mid-box">命中2位数</div>
                                <div class="right-box"> 5.0 倍</div>
                            </li>
                            <li>
                                <div class="left-box">3位数</div>
                                <div class="mid-box">命中3位数</div>
                                <div class="right-box"> 15.0 倍</div>
                            </li>
                            <li>
                                <div class="left-box">4位数</div>
                                <div class="mid-box">命中4位数</div>
                                <div class="right-box"> 50.0 倍</div>
                            </li>
                            <li>
                                <div class="left-box">5位数</div>
                                <div class="mid-box">命中5位数</div>
                                <div class="right-box"> 100.0 倍</div>
                            </li>
                        </ul>
                    </div>
                    <div class="cd-box">
                        未押中：三顺,四顺,五顺,三豹,四豹,五豹,520,1314   奖励11.88 - 1288.88 现金
                    </div>
