        <v-prompt v-show="isShowPrompt" :is-show="isShowPrompt" @okfn="promptOkFn">
            <span slot="header">信息提示 </span>
            <span slot="body"><div v-html="promptMsg"></div></span>
        </v-prompt>

        <v-loading v-show="isShowLoading">
            <span slot="text">吃鸡中...</span>
