<script>
import Dialog from '@/components/common/Dialog';
import MatchOne from '@/components/matchs/MatchOne';
import MatchTwo from '@/components/matchs/MatchTwo';
import MatchThree from '@/components/matchs/MatchThree';
import Prompt from '@/components/common/Prompt';
import Loading from '@/components/common/Loading';
import {startGame, queryBalance} from '../request/api';
export default {
  name: 'Match',
  components: {
      'v-matchone': MatchOne,
      'v-dialog': Dialog,
      'v-prompt': Prompt,
      'v-loading': Loading,
      MatchTwo,
      MatchThree
  },
  data () {
    return {
        isShowRule:false,
        isShowLoading:false,// 显示loading
        isShowDialog: false,
        downCountMod: '1',
        countSel: 1,
        downCount: 20,
        matchone: 'v-matchone',
        MatchTwo: 'MatchTwo',
        MatchThree: 'MatchThree',
        currentView: 'v-matchone',
        dialog: 'v-dialog',
        promptMsg:'加载中...',
        isShowPrompt: false, //显示提示层
        typeAct:1,// 当前选择类型
        curBetNum:null,// 当前用户下注值
        curLevel:null,//当别级别
        balance: 0, // 用户查询余额
        curDownFee: '10.00', // 当前下注费,
        preIncome: 0,// 预计收益
        globalOddsNum:{ // 预计收益全局赔率
            '1':{'1':200,'2':500,'3':1000,'4':2500,'5':10000},
            '3':{'0':0,'1':0,'2':500,'3':1500,'4':5000,'5':10000}
            },
        /**
         * 比赛调出返回结果
         * */
        totalBalance: 0, // 本场结束后余额
        redPacket: 0, // 红包
        matchResult: 0, // 本场夺宝结果
        orderOn:0, // 微信订单号
        userVal: '0', // 下注的值
        oddsNum :'',// 赔率
        keyNum:'',// 下注结果值
        luckNum:'',
        curPoundage: '0.00', // 本场手续费
        incomeResult:'加载中...', // 本场收益结果
        count:'加载中...'

    };
  },
  created () {
      this.queryBalanceFun();
      this.toggleTyps(this.matchone, 1);
  },
    methods: {
        /**
         * 查询余额
         * */
        queryBalanceFun() {
            queryBalance().then(data => {
                  let { msg, code, result } = data
                  if (code === 200) {
                     // var bal = parseFloat(result.balance).toFixed(2);
                      this.redPacket = parseFloat(result.red_packet).toFixed(2);
                      this.balance = parseFloat(result.balance).toFixed(2);
                      this.count = 100 - (result.macthcount + result.witharwcount);
                  } else {
                      console.log(msg);
                  }
              });
        },
        showRule(){
            this.isShowRule = true;
        },
        promptOkFn(){
            this.isShowPrompt = false
        },
        promptAlert(max){

            this.promptMsg = '最多只能选择 '+max+' 个数';
            this.isShowPrompt = true;
            return;
        }
        ,
        callBetFn(type,num) {
            switch (type){
                case '1':
                    this.curBetNum = num;
                    break;
                case '2':
                    this.curLevel = num;
                    break;
                case '3':
                    this.curLevel = null;
                    this.curBetNum = num;
                    break;
            }
            this.setPreIncome();
        },
        /**
         * 设置预计收益
         **/
        setPreIncome(){
            //显示预计收益
            var curOddsNums = this.globalOddsNum[this.typeAct];

            if(this.curBetNum != null && this.typeAct == 3){
                curOddsNums = curOddsNums[this.curBetNum.length];
                this.preIncome =  ((curOddsNums * this.curDownFee)/100).toFixed(2);
                this.curPoundage = ((this.curDownFee*10)/10).toFixed(2);
            }else if(this.typeAct == 1 && this.curLevel != null){
                curOddsNums = curOddsNums[this.curLevel];
                this.preIncome =  ((curOddsNums * this.curDownFee)/100).toFixed(2);
                this.curPoundage = ((this.curDownFee*10)/10).toFixed(2);
            }else{
                this.preIncome = '0.00';
                this.curPoundage = '0.00';
            }

        },
        /**
         * 切换tab
         * */
        toggleTyps(tabTxt, index) {
            this.typeAct = index;
            let res = localStorage.getItem("type"+index);
            if(!res){
                this.isShowRule = true;
                localStorage.setItem('type'+index,true);
            }
            this.currentView = tabTxt;
            this.curBetNum = null;
            this.setPreIncome();
        },
        showdialog() {
            this.isShowDialog = true;
        },
        closedialog(msg) {
            this.isShowDialog = false;
        },
        // 改变值的事件
        changDownFee(){
            this.curDownFee = parseInt(this.downCountMod) * 10;
            this.setPreIncome();
        }
        ,
        /**
         * 设置注下数
         */
        setDownCount(type) {
            switch (type){
                case 'minus':
                    this.downCountMod >1 ? this.downCountMod-- : this.downCountMod;
                    break;
                case 'plus':
                    this.downCountMod < this.downCount ? this.downCountMod++ : this.downCountMod;
                    break;
                case 'min':
                    this.downCountMod = 1;
                    break;
                case 'max':
                    this.downCountMod = this.downCount;
                    break;
            }
            //设置下注成费
            this.curDownFee = parseInt(this.downCountMod) * 10;
            this.curDownFee = this.curDownFee.toFixed(2);

            this.setPreIncome();
        },
        /**
         * 提交游戏
         */
        submitBet(){

            if(this.typeAct == 1 && this.curLevel == null ){
                this.promptMsg = '请选择吃鸡类型';
                this.isShowPrompt = true;
                return;
            }
            //不合法的数据
            if(this.curBetNum==null){
                this.promptMsg = '请选择吃鸡号码';
                this.isShowPrompt = true;
                return;
            }
            //下注数不合法
            if(!(this.downCountMod > 0 && this.downCountMod <= 20)){
                this.promptMsg = '下注数不合法，请重新提交';
                this.isShowPrompt = true;
                return;
            }
            if(this.typeAct ==3 && this.curBetNum.length <=1){ //最少选两个
                this.promptMsg = '多雷场最少选两个号码';
                this.isShowPrompt = true;
                return;
            }
            /**
             * jc_type:1 //1=单雷场 ， 3=多雷场
             user_val:1 //用户下注的值
             num:1 //用户下注的次数
             * @type {{jc_type: string, user_val: string, num: string}}
             */
            var params = {jc_type:this.typeAct, user_val:this.curBetNum, num:this.downCountMod,level:this.curLevel}


            if(this.isShowLoading){
                return;
            }
            this.isShowLoading = true;
            startGame(params).then(data => {
                this.isShowLoading = false;
                let { msg,msgcode, code, result } = data
                if (code == 200) {
                    this.queryBalanceFun();

                    this.matchResult = result.match_result; // 本场夺宝结果
                    this.orderOn = result.orderId//.substr(0,result.orderId.length-5); // 微信订单号
                    this.gameLevel = result.level; //单雷场游戏类型
                    this.keyNum = result.keyNum;// 夺宝值
                    var luckmp = {1:'豹子',2:'顺子',3:"亮号"};
                    this.luckNum = luckmp[result.luckNum];
                   if (this.typeAct < 3) {
                       this.userVal = this.curBetNum; // 下注的值 还需要修改
                   } else {
                       this.userVal = this.curBetNum.split('').join(','); // 下注的值 还需要修改
                   }
                    this.oddsNum = result.oddsNum;// 赔率
                    this.curPoundage = (parseFloat(result.curPoundage)/100).toFixed(2);
                    if(this.matchResult ==1){
                        this.incomeResult = '+'+(this.oddsNum * (this.downCountMod*10) / 100)
                    }else if(this.matchResult ==3){
                        this.incomeResult = '+'+(this.oddsNum * (this.downCountMod*10) / 100)
                    }else{
                        this.incomeResult = '-'+(this.downCountMod*10)
                    }
                    this.isShowDialog = true;
                } else {
                    var prsmsg = '<div class="prompt-with">' +
                        '<div class="p-line" style="display: flex">' +
                        '<div class="l-item" style="flex: 2; text-align: right">当前可用余额：</div>' +
                        '<div class="r-item" style="flex: 1;text-align: right; color: #f4733b">'+this.balance+'元</div> </div> ' +
                        '<div class="p-line"  style="display: flex"> ' +
                        '<div class="l-item" style="flex: 2;text-align: right"> 红包(可抵手续费)：</div> ' +
                        '<div class="r-item" style="flex: 1;text-align: right">'+this.redPacket+'元</div> </div> ' +
                        '<div class="p-line"  style="display: flex"> <div class="l-item" style="flex: 2;text-align: right">下注费：</div> ' +
                        '<div class="r-item" style="flex: 1;text-align: right">'+this.curDownFee+'元</div> </div> ' +
                        '<div class="p-line"  style="display: flex"> <div class="l-item" style="flex: 2;text-align: right">手续费(10%)：</div>' +
                        ' <div class="r-item" style="flex: 1;text-align: right">'+(this.curPoundage/10).toFixed(2)+'元</div> </div>' +
                        ' <div class="p-line" style="display: flex"> ' +
                        '<div class="l-item" style="flex: 2; font-size: 11px;text-align: right">通知费(夺宝自动打款微信钱包)：</div>' +
                        ' <div class="r-item" style="flex: 1;text-align: right">1.00元</div> </div> ' +
                        '<div class="p-line"  style="display: flex"> <div class="l-item" style="flex: 1; font-size: 20px">'+msg+'</div> </div> </div>';
                    if('NOT_SUFFICIENT_FUNDS' == msgcode){
                        this.promptMsg = prsmsg;
                    }else{
                        this.promptMsg = msg;
                    }

                    this.isShowPrompt = true;
                }
            });
        }
    },
    watch: {
        'isShowDialog' : function (newValue, oldValue) {
            // console.log(newValue,oldValue);
        }
  }
};
</script>

