        color: #f4733b;
        font-size: 22px;
    }
    .bot-item-plus{
        flex: 2;
        position: relative;
    }
    .bot-item-plus:before{
        height: 1px;
        width: 16px;
        background-color: #666;
        top: 0;
        left: 0;
        right: 0;
        margin: auto;
        bottom: 0;
        content: '';
    }
    .bot-item-plus:after{
        width: 1px;
        height: 16px;
        background-color: #666;
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        margin: auto;
        content: '';
    }

    .bot-item-max{
        flex: 2;
        background-color: #f4733b;
        color: #fff;
    }
    /*下注 end */

    .match-box{
        margin-top: 10px;
        min-height: 182px;
        padding: 5px 20px;
    }
    .down-box{
        padding-top: 10px;
        height:50px;
        line-height: 50px;

    }
    .down-btn{
        color: #fff;
        text-shadow: 1px 1px 1px #000;
        background: -webkit-gradient(linear,0 0,0 100%,from(#df9e00),to(#cd9200));;
        font-size: 22px;
    }
    .dialog-header{ text-align: center; height: 70px;}
    .dialog-header img{ width: 120px}
    .dialog button{ height: 50px; width: 100%;background-color: #43ac3f;border-radius: 4px; color: #fff; font-size: 22px}
    .dialog-name{ margin-top: 24px;font-size: 24px;}
    .dialog .dialog-title{ height: 40px}
    .dialog-body{padding-bottom: 20px}
    .dialog-body .wx-title { line-height: 26px}
    .dialog-body .wx-order{ height: 22px; line-height: 22px; font-size: 14px;color: #5b6a92}
    .dialog-body .wx-order span{ color: #f4733b;font-size: 24px}

    .dialog-body .wx-result{
        padding-top: 10px;
        padding-bottom: 10px;
        display: flex;
    }
    .dialog-body .wx-result .res-title{
        flex: 1;
    }
    .dialog-body .wx-result .res-con{
        flex: 1;
        color: #f4733b;
    }
    .wx-des{ font-size: 14px; line-height: 20px; height: 24px; padding-top: 5px}
    .wx-des span{color:#f4733b}
    .details_line{ padding-top: 10px}
    .line-item{
        display: flex;
        line-height: 28px;
        padding: 0 40px;
    }
    .line-item .left-title{
        flex: 1;
        text-align: left;
        color: #929090;
        font-size: 16px;
    }
    .line-item .right-value{
        flex: 1;
        text-align: right;
        font-size: 16px;
        color: #b9b6b6;
    }
    .line-item .right-value.fee{

        color: #f4733b;
    }

    /**
    规则 说明
     */
    .rule-header{ height: 20px;
        font-weight: bold;
        padding-bottom: 4px;
        border-bottom: solid 1px #dadada;
    }

    .mid_panel .items{
        margin-bottom: 10px;
    }
    .mid_panel .items .title{
        height: 30px;
        line-height: 30px;
        color: #3f3d3d;
        font-weight: bold;
    }
    .mid_panel .items .des{
        line-height: 20px;
        color: #929090;
        word-wrap: break-word;
    }
    .mid_panel .items .des span{
        color: #f4733b;
    }


    .mid_panel .list{

    }
    .mid_panel .cd-box{
        font-size: 12px;
        line-height: 20px;
        color: #FF0000;
    }

    .mid_panel .list li{
        display: flex;
        height: 30px;
        line-height: 30px;
        border-bottom: dashed 1px #dadada;
    }

    .mid_panel .list .li-title{
        height: 40px;
        line-height:40px;

    }
    .mid_panel .list .li-choose{
        color: #f4733b;
    }
    .mid_panel .list li:last-child{
        border-bottom: 0;
    }
    .mid_panel .list li .left-title{
        flex:1;
    }

    .mid_panel .list li .right-title{
        flex:1;
    }

    .mid_panel .list li .left-box{
        flex:1;
        text-align: left;
        font-size: 14px;
    }
    .mid_panel .list li .mid-box{
        flex:1;
        font-size: 14px;
    }
    .mid_panel .list li .right-box{
        flex:1;
        text-align: right;
        font-size: 14px;

    }

    .mid_panel .list li .right-box.tie{
        color: #43ac3f;
    }
    @media only screen and (max-height: 620px){
        .head-choose-type{
            padding-top: 6px;
            height: 40px;
        }
        .match-box{
            min-height: 172px;
        }
        .pour-box{
            height: 86px;
            margin-top: 8px;
            padding: 6px 20px;
        }
    }
    @media only screen and (max-height: 600px){
        .head-choose-type{
            padding-top: 6px;
            height: 40px;
        }
        .match-box{
            min-height: 172px;
        }
        .pour-box{
            height: 86px;
            margin-top: 8px;
            padding: 6px 20px;
        }
    }
    @media only screen and (max-height: 520px){
        .head-choose-type{
            padding-top: 4px;
            height: 40px;
        }
        .match-box{
            min-height: 130px;
        }
        .pour-box{
            height: 84px;
            margin-top: 6px;
            padding: 6px 20px;
        }
        .income-box .lines li{
            height: 20px;
            line-height: 20px;
        }
        .income-box .lines li .left-item{
            font-size: 12px;
        }

    }
    /**
    余额不足
     */
    .prompt-with{

    }
    .prompt-with .p-line{
        display: flex;
    }
    .prompt-with .l-item{
        flex: 1;
    }
    .prompt-with .r-item{
        flex: 1;
    }
</style>



