<template>
    <div class="main_panel">
        <div class="type-head">
            吃鸡类型
        </div>
        <div class="sel-type">
            <ul>
                <li v-for="item in selType"  :class="{'act': item.val == selAct}" :val="item.val" @click="downNum(item.val)">{{item.name}}</li>
            </ul>
        </div>
        <div class="btn-head">
            吃鸡号码
        </div>
        <div class="btns-box">
            <div class="item-btn" v-for="item in nums"><div><span :class="{'act': item == act}" @click="pourNum(item)">{{item}}</span></div></div>
        </div>
    </div>
</template>

<script>
    export default {
        name: 'MatchThree',

        data () {
            return {
                selType:[{name:'1中1',val:1},{name:'1中2',val:2},{name:'1中3',val:3},{name:'1中4',val:4},{name:'1中5',val:5}],
                nums:[1,2,3,4,5,6,7,8,9,0],
                limit:0,
                max:5,
                values:[],
                act:null,
                selAct:null
            };
        },
        created () {
        },
        methods:{
            pourNum(item) {
                this.act = item;
                this.$emit('parentfn','1',item);
            },
            downNum(val){
                this.selAct = val;
                this.$emit('parentfn','2',val);
            }
        }
    };
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    .type-head{
        color: #929090;
        font-size: 14px;
        padding-bottom: 10px;
    }
    .btn-head{
        color: #929090;
        font-size: 14px;
        padding-top: 10px;
        padding-bottom: 0px;
    }
    .sel-type ul{ display: flex}
    .sel-type li{
        flex: 1;
        color: #fff;
        background-color:#b6b6b6;
        margin: 0 5px;
        line-height: 26px;
        border-radius: 4px;
    }
    .sel-type li.act{
        background-color: #f4733b;
        color: #fff;
    }
    .btns-box{
        display: flex;
        flex-flow: row wrap;
        padding-top: 2px;
    }
    .btns-box .item-btn{

        width: 20%;
        padding: 5px;
        box-sizing: border-box;
    }
    .btns-box .item-btn div{
        width: 100%;
        height: 0;
        padding-bottom: 100%;
        position: relative;
        box-sizing: border-box;

    }
    .btns-box .item-btn span{
        position: absolute;
        display: flex;
        justify-content:center;
        align-items:center;
        width: 100%;
        height: 100%;
        font-size: 20px;
        border-radius: 40px;
        color: #f4733b;
        background-image: url("../../assets/img/common/thunder-box.png");
        background-size: cover;
    }
    .btns-box .item-btn span.act{
        color: #fff;
        background-image: url("../../assets/img/common/thunder2.png");

    }
    @media only screen and (max-height: 630px){
        .btns-box{
            padding-top: 0px;
        }
    }
    @media only screen and (max-height: 600px){
        .btns-box{
            padding-top: 0px;
        }
        .type-head{ display: none}
        .btn-head{ display: none}
        .sel-type li{
            font-size: 12px
        }
        .btns-box{
            padding: 0 10px;
        }
    }
    @media only screen and (max-height: 520px){
        .btns-box{
            padding-top: 0px;
        }
    }
</style>



