<template>
  <div class="head-balance comm_bg">
    <div class="top-title">
      账户余额（元）
    </div>
    <div class="bottom-fee">
      {{balance}}
    </div>
  </div>
</template>

<script>
import {queryBalance} from '../../request/api';
export default {
  name: 'HeadBalance',

  data () {
    return {
        balance: '加载中...'
    };
  },
    created () {
        queryBalance().then(data => {
            let { msg, code, result } = data
            if (code === 200) {
                var bal = parseFloat(result.balance);
                var redPacket = parseFloat(result.red_packet);
                this.balance = (bal + redPacket).toFixed(2);
                this.$emit('okfn', result);
            } else {
                console.log(msg);
            }
        });
    },
    //设置金额
    setbalance(fee){
      this.balance = fee;
    }
};
</script>

<style scoped>
  .head-balance{
    height: 70px;
  }
  .head-balance .top-title{
    padding-top: 6px;
    height: 25px;
    line-height: 25px;
    font-size: 12px;
    color: #929090;
  }
  .head-balance .bottom-fee{
    line-height: 25px;
    color: #f4733b;
    font-size: 24px;
  }
</style>



