<script>
import {queryUser} from '../request/api';
export default {
  name: 'Mycenter',
  data () {
    return {
        qrcodeurl:'/service/Public/loading.gif',
    };
  },
  created () {
      queryUser().then(data => {
          let { msg, code,result } = data
          if (code == 200) {
                this.qrcodeurl = '/service/home/user/createqrcode?userid='+result.userid;
          } else {
              window.location.href = requestLogin+"?parent_id="+this.$route.query.id;
          }
      });
  },
