<template>
    <div class="main-panel">
        <div class="headers-box">
            <div class="recommend" :class="{act:act ==1}" @click="rankChoose(1)"> 同类精彩小游戏</div>
        </div>
        <div class="list-box">
            <ul class="tw-list" ref="listbox">
                <li class="comm_bg">
                    <a href="http://vip.chuangyezhebao.com?parent_id=100000">
                        尾号夺宝，尾号竞猜 -------------&gt;点击进去
                    </a>
                </li>
                <li class="comm_bg">
                    <a href="http://vip.skfskf.net?parent_id=100000">
                        天天抓英雄，10元撸1000 -------------&gt;点击进去
                    </a>
                </li>
            </ul>
        </div>
    </div>
</template>

<script>
 import {rankingIndex} from '../request/api';
export default {
  name: 'Ranking',
  data () {
    return {
        listData: null,
        act: 1

    };
  },
  created () {
    this.recommndRanking(1)
  },
    methods: {
        recommndRanking (type){
            var params = {type:type};
            rankingIndex(params).then(data => {
                let { msg, code, result } = data
                if (code === 200) {
                    this.listData = result.list;
                } else {
                    console.log(msg);
                }
            });
        },
        /**
         * 选择排行榜类型
         */
        rankChoose (type) {
            this.act = type;
            this.recommndRanking (type);
        }
    },
    mounted () {
        this.$refs.listbox.addEventListener('touchmove',function(e){
            e.stopPropagation();
        });
    }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    .main-panel{
        padding: 10px;
    }
    .headers-box{
        display: flex;
        height: 60px;
        line-height: 60px;
    }
    .headers-box .act{
        background-color: #f4733b;
        color: #fff;
    }
    .recommend{
        flex: 1;
        margin-right: 5px;
    }
    .match{
        flex: 1;
        margin-left: 5px;
    }
    .tw-list{ margin-top: 20px}
    .tw-list li{
        display: flex;
        margin-bottom: 10px;
        line-height: 30px;
        padding: 10px;
        color: #eaecec;
    }


</style>



