<template>
    <div class="main_panel">
        <div class="head-title comm_bg">
            <img width="120" src="../assets/img/common/alipay_ico.png" />
        </div>
        <div class="head-fee comm_bg">
            <div class="top-title">
                支付金额(帐户金额为实付金额)
            </div>
            <div class="bottom-fee">
                {{fee}}
            </div>
        </div>
        <div class="mid-box comm_bg">
            <div class="order-id">订单号：<span>{{orederOn}}</span></div>
            <div class="prompt">正在加载中...</div>
        </div>

    </div>
</template>

<script>
    import {reqTopup, callTopup} from '../request/api';
    export default {
        name: 'Home',
        data () {
            return {
                downCountBox:true,//显示倒计时
                minute:"00",
                second:"00",
                timersRes:null,
                curAct: '20',
                balance: 0,
                qrcode: '',
                orederOn: '加载中',
                create_time: '加载中',
                fee: '加载中..',
                payState: false,
                url:null,
                qrcodeExpire: false
            };
        },
        created () {
            //console.log(this.$route.params.num)
            if (!this.$route.params.num) {
                this.$router.push({name: 'topup', params: { num: this.curAct,type:3}});
                return;
            }
            var params = {num: this.$route.params.num,type:'3'};
            reqTopup(params).then(data => {
                // NProgress.done();
                let { msg, code, result } = data
                if (code == 200) {
                    this.qrcode = result.qrcode;
                    this.url = result.url;
                    this.orederOn = result.oreder_on;
                    this.create_time = result.create_time;
                    this.fee = parseFloat(result.fee).toFixed(2);
                    //this.ajaxQuery(result.oreder_on, 1);
                    this.$route.query.order = result.url;
                    window.location.href = './static/gotoalipay.html?url='+result.url+'&fee='+this.fee+'&order='+result.oreder_on;
                    //this.$router.push({name: 'gotoalipay',params: { fee: this.fee,qrcode:result.qrcode,orederon:result.oreder_on}, query: { url: result.url}});
                } else {
                    console.log(msg);
                }
            });
            var intDiff = parseInt(600);//倒计时总秒数量
            this.timer(intDiff);
        },
        methods: {
            selectedFee(cur) {
                this.curAct = cur;
            },
            coundown(){
                var nowTime = new Date();
            },
            timer(intDiff){
                var self = this;
                self.timersRes = window.setInterval(function(){
                    var day=0, hour=0, minute=0, second=0;//时间默认值
                    if(intDiff > 0){
                        day = Math.floor(intDiff / (60 * 60 * 24));
                        hour = Math.floor(intDiff / (60 * 60)) - (day * 24);
                        minute = Math.floor(intDiff / 60) - (day * 24 * 60) - (hour * 60);
                        second = Math.floor(intDiff) - (day * 24 * 60 * 60) - (hour * 60 * 60) - (minute * 60);
                    }
                    if (minute <= 9) minute = '0' + minute;
                    if (second <= 9) second = '0' + second;
                    self.minute = minute;
                    self.second = second;
                    // console.log(day+"天",hour+'时',minute+'分',second+'秒')
                    if (minute <= 0 && second <= 0){
                        window.clearInterval(self.timersRes);
                        self.qrcodeExpire = true;
                    }
                    intDiff--;
                }, 1000);
            },
            ajaxQuery(orderOn, index) {
                var self = this;
                var params = {order: orderOn};
                callTopup(params).then(data => {
                    // NProgress.done();
                    let { msg, code, result } = data
                    if (code == 200) {
                        self.payState = true;
                        window.clearInterval(self.timersRes);
                        self.downCountBox = false;
                        /*   window.setTimeout(() => {
                         self.$router.push({ name: 'topup', params: { num: this.curAct }});
                         }, 2000);*/

                    } else {
                        let self = this;
                        if (self && !self._isDestroyed) {
                            setTimeout(() => {
                                if (self && !self._isDestroyed) {
                                    if (index < 200) {
                                        self.ajaxQuery(orderOn, ++index);
                                    }else{
                                        self.qrcodeExpire = true;
                                    }
                                }
                            }, 3000);
                        }
                    }
                });
            }
        }
    };
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    .main_panel{ padding: 20px}
    .head-title{
        height: 70px;
        line-height: 70px;
        font-size: 20px;
        color: #929090;

    }
    .head-fee{
        margin-top: 10px;
        height: 70px;
    }
    .head-fee .top-title{
        padding-top: 6px;
        height: 25px;
        line-height: 25px;
        font-size: 12px;
        color: #929090;
    }
    .head-fee .bottom-fee{
        line-height: 25px;
        color: #f4733b;
        font-size: 24px;
    }
    .head-title img{
        vertical-align: -8px;
    }

    .mid-box{
        min-height: 330px;
        margin-top:10px;
    }
    .mid-box .order-id{ line-height: 40px; color: #929090}
    .mid-box .qr-code{
        width: 160px;
        height: 160px;
        background-color: #ccc;
        margin: 0 auto;
        position: relative;
    }
    .pay-succeed,.pay-qrcode-excpire{
        position: absolute;
    }
    .mid-box .qr-code img{
        width: 100%;
    }
    .mid-box .prompt{
        line-height: 40px;
        color: #44af35;
    }
    .mid-box .prompt span{
        color: #f4733b;
    }
    .mid-box .warn{
        font-size: 12px;
        color: #929090;
    }
    .mid-box .prompt-red{
        padding-top: 5px;
        font-size: 12px;
        line-height: 20px;
        color: #f4733b;
    }
    .mid-box .down-count{
        padding-top: 20px;
        font-size: 30px;
        color: #929090;
    }

</style>



