<template>
    <transition name="prompt-fade">
    <div class="loadig_panel">
        <div class="loading" >
            <div class="loading-gif">
                <img src="../../assets/img/common/loading.gif" />
            </div>
            <div class="loading-text">
                <slot name="text"></slot>
            </div>
        </div>
        <div class="prompt-overlay"></div>
    </div>
    </transition>
</template>

<script>
export default {
  name: 'Loading',
    props: {
        yejieMsg: {
            type: String,
            default: ''
        },
        isShow: {
            type: Boolean,
            default: false
        }
    },
  data () {
    return {
        shows: false
    };
  },
  methods: {
      close: function() {
          this.shows = false;
      },
      show: function() {
          this.shows = true;
      }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    /**
 加载层
  **/
    .loading {
        width: 85%;
        position: fixed;
        left: 50%;
        top: 50%;
        min-height: 100px;
        margin-top: -100px;
        transform: translateX(-50%);
        z-index: 2000;
        visibility: visible;
        backface-visibility: hidden;
    }
    .prompt-overlay {
        content: "";
        position: fixed;
        visibility: hidden;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        z-index: 1000;
        opacity: 0;
        background: rgba(0, 0, 0, 0.7);
        transition: all .6s;
        opacity: 1;
        visibility: visible;
    }
    .loading-text{ padding-top:10px;color: #fff;font-size: 20px}

</style>



