<template>
    <transition name="dialog-fade">
    <div class="dialogs">
        <div class="dialog" >
            <div class="dialog-content" :class="{act:show}">

                <slot name="header"></slot>
                <slot name="body"></slot>
                <slot name="footer"></slot>
            </div>
        </div>
        <div class="dialog-overlay" @click="closeDialog"></div>
    </div>
    </transition>
</template>

<script>
export default {
  name: 'Dialog',
    props: {
        yejieMsg: {
            type: String,
            default: ''
        },
        isShow: {
            type: Boolean,
            default: false
        }
    },
  data () {
    return {
        show: false,
        testData: 'this is testing data'
    };
  },
    created () {
        this.show = this.isShow;
    },
  methods: {
      close() {
          this.show = false;
      },
      shows() {
          this.show = true;
      },
      closeDialog() {
          this.$emit('upup', this.testData);
      }
  },watch: {
        'isShow' : function (newValue, oldValue) {
            var self = this;
            setTimeout(function(){
                self.show =  newValue;
            },50);
        }
    }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    /**
 弹出层
  **/
    .dialog {
        width: 85%;
        position: fixed;
        left: 50%;
        top: 50%;
        transform: translate(-50%,-50%);
        z-index: 2000;
        visibility: visible;
        backface-visibility: hidden;
        perspective: 1300px;
    }
    .dialog-content {
        padding: 20px;
        border-radius: 4px;
        background: #fff;
        overflow: hidden;
        box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1);
        transition: .5s ease-in-out;
        opacity: 0;

    }
    .dialog-content.act {
        opacity: 1;
    }
    .dialog-overlay {
        content: "";
        position: fixed;
        visibility: hidden;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        z-index: 1000;
        opacity: 0;
        background: rgba(0, 0, 0, 0.5);
        transition: all .6s;
        opacity: 1;
        visibility: visible;
    }

</style>



