<template>
    <div class="main_panel">
        <div class="btns-box">
            <div class="item-btn max" :class="{activate:itemBtnAct == 1}" @click="pourNum(1)">单</div>
            <div class="item-btn min" :class="{activate:itemBtnAct == 2}" @click="pourNum(2)">双</div>
        </div>
    </div>
</template>

<script>
export default {
  name: 'MatchTwo',

  data () {
    return {
        itemBtnAct: 0
    };
  },
  created () {
   /* console.log('yejie')
    this.$http.get('/request/login').then((response) => {
      console.log(response)
      console.log(response.body.data)
    }) */
  },
    methods:{
        pourNum(num) {
            this.itemBtnAct= num;
            this.$emit('parentfn','2',num);
        }
    }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    .btns-box{
        display: flex;
        padding-top: 32px;
    }
    .btns-box .item-btn{
        flex: 1;
        height: 50px;
        line-height: 50px;
        border-radius: 4px;
    }
    .btns-box .item-btn{
        color: #65c2be;
        background-color: #eaecec;
        margin-right: 10px;
        font-size: 22px;
    }
    .btns-box .item-btn.activate{
        color: #eaecec;
        background-color: #65c2be;
    }
    .btns-box .item-btn:last-child{
        margin-right: 0;
    }
    @media only screen and (max-height: 600px){
        .btns-box{
            padding-top: 10px;
        }
    }
    @media only screen and (max-height: 520px){
        .btns-box{
            padding-top: 0px;
        }
    }
</style>



