<template>
<div class="page">
  <header>
      <span @click="$router.back()">返回</span>
      <span>{{title}}</span>
      <span></span>
  </header>
  <div class="content">
      <slot></slot>
  </div>
</div>
</template>

<script>
export default {
  props: {
      title:{
          type:String,
          default:""
      }
  }
}
</script>

<style lang="stylus" scoped>
.page{
 position absolute
 width 100%
 top 0
 left 0
 bottom 50px
 z-index 11
 header{
     padding 0 20px
     height 40px
     color #333
     background #ffffff
     display flex
     justify-content space-between 
     align-items center
 }
}
</style>



