<template>
    <div class="main_panel">
        <v-balance></v-balance>
        <div class="mid-choose">
            <div class="withdraw-type">
                即将开放
            </div>
        </div>


    </div>
</template>

<script>
import HeadBalance from '@/components/common/HeadBalance';
import {queryBalance} from '../request/api';
export default {
    name: 'Home',
    components: {
        'v-balance': HeadBalance
    },
  data () {
    return {
        curAct: '20',
        chooseFee: [20, 50, 100, 150, 200, 500],
        balance: 0
    };
  },
  created () {
      queryBalance().then(data => {
          // NProgress.done();
          let { msg, code, result } = data
          if (code == 200) {
                this.balance = result.balance;
          } else {
              console.log(msg);
          }
      });
  },
    methods: {

    }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    .main_panel{ padding: 20px}

    .mid-choose{
        padding-top: 32px;
    }
    .withdraw-title{
        font-size: 14px;
        color: #929090;
        text-align: left;
    }
    .withdraw-type ul{
        display: flex;
        flex-flow: row wrap;
        padding-top: 5px;
    }
    .withdraw-type li{
        width: 100%;
        height: 60px;
        padding: 5px;
        margin-top: 10px;
        text-align: left;
        line-height: 60px;
        text-indent: 70px;
        position: relative;
    }
    .withdraw-type li .icon{
        position: absolute;
        display: flex;
        justify-content:center;
        align-items:center;
        height: 60px;
        padding-left: 20px;
    }
    .withdraw-type li i{
        position: absolute;
        top: 24px;
        right: 10px;
        padding: 10px;
        background: url('../assets/img/common/arrows.png') no-repeat;
        background-size: contain;
    }

    .withdraw-type li .icon img{ width: 40px; height: 40px}


</style>



