<template>
    <div class="main_panel">
        <div class="btns-box">
            <div class="item-btn" v-for="item in nums"><div><span :class="{'act': item.checked}" @click="pourNum(item)" >{{item.val}}</span></div></div>
        </div>
    </div>
</template>

<script>
export default {
  name: 'MatchThree',

  data () {
    return {
        nums:[{val:'1'},{val:'2'},{val:'3'},{val:'4'},{val:'5'},{val:'6'},{val:'7'},{val:'8'},{val:'9'},{val:'0'}],
        limit:0,
        max:5,
        values:[]
    };
  },
  created () {
  },
    methods:{
        pourNum(item) {
            this.limit = 0;
            this.values = [];
            for(var i = 0;i<this.nums.length;i++){
                if(this.nums[i].checked){
                    this.limit++;
                }
            }
            if(item.checked){
                item.checked =!item.checked;
            }else{

                if(this.limit>=this.max){
                   // alert("最多只能选择"+this.max);
                    this.$emit('parentalert',this.max);
                    return;
                }

                this.$set(item,"checked",true);
                this.limit++

            }
            for(var i = 0;i<this.nums.length;i++){
                if(this.nums[i].checked){
                    this.values.push(this.nums[i].val);
                }
            }
            this.$emit('parentfn','3',this.values.join(''));
        }
    }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    .btns-box{
        display: flex;
        flex-flow: row wrap;
        padding-top: 2px;
    }
    .btns-box .item-btn{

        width: 20%;
        padding: 5px;
        box-sizing: border-box;
    }
    .btns-box .item-btn div{
        width: 100%;
        height: 0;
        padding-bottom: 100%;
        position: relative;
        box-sizing: border-box;

    }
    .btns-box .item-btn span{
        position: absolute;
        display: flex;
        justify-content:center;
        align-items:center;
        width: 100%;
        height: 100%;
        font-size: 20px;
        border-radius: 40px;
        color: #5b6a92;
        background-color: #000;
        background-image: url("../../assets/img/common/thunder-box.png");
        background-size: cover;

    }
    .btns-box .item-btn span.act{
        color: #fff;
        background-image: url("../../assets/img/common/thunder.png");
    }
    @media only screen and (max-height: 600px){
        .btns-box{
            padding-top: 0px;
        }
    }
    @media only screen and (max-height: 520px){
        .btns-box{
            padding-top: 0px;
        }
    }
</style>



