<template>
  <div class="loading-small" :class="classname">
    <img src="./loading.gif" v-if="imgshow">
    <p class="desc">{{title}}</p>
  </div>
</template>
<script type="text/ecmascript-6">
  export default {
    props: {
      title: {
        type: String,
        default: '正在载入...'
      },
      imgshow:{
        type: Boolean,
        default: true
      },
      size:{
        type: String,
        default: 'small' 
      }
    },
    computed:{	
      classname:function(){
        switch (this.size) {
          case 'large':
          return {
            large : true
          }
            break;
          default:
            break;
        }
      }
    }
  }
</script>
<style scoped lang="stylus" rel="stylesheet/stylus"> 
  .loading-small
    text-align: center
    display flex
    justify-content center
    align-items center
    width 100%
    height 40px
    img
     width 20px
     height 20px
     margin-right 5px
    .desc
      font-size: 11px
      color: #7d7d7d
  .large
    position fixed
    z-index 1999
    background-color rgba(0,0,0,.5)
    width 100%
    height 100%
    top 0
    left 0
    img
     width 40px
     height 40px
    .desc
     font-size 24px
</style>



