<template>
  <div ref="wrapper" class="better-scroll-root">
    <div class="content-bg better-scroll-container">  <!--如果需要调滚动内容的背景色，则改该节点的背景色-->
      <slot></slot>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  import BScroll from 'better-scroll'
  export default {
    props: {
      probeType: {
        type: Number,
        default: 1
      },
      scrollX: {
        type: Boolean,
        default: false
      },
      scrollY:{
        type: Boolean,
        default: true
      },
      click: {
        type: Boolean,
        default: true
      },
      listenScroll: {
        type: Boolean,
        default: false
      },
      data: {
        type: Array,
        default: null
      },
      pullup: {
        //是否派发滚动到底部的事件，用于上拉加载
        type: Boolean,
        default: false
      },
      pulldown: {
       type: Boolean,
       default: false
      },
      beforeScroll: {
        type: Boolean,
        default: false
      },
      refreshDelay: {
        type: Number,
        default: 20
      },
      /**
       * 如果启用loading交互，传递loading的状态
       * isShow: false
       * showIcon: false,    // 是否显示loading的icon
       * status: ''  // '正在加载...', '刷新成功', '刷新失败', ''
       */
      loadingStatus: {
          type: Object,
          default: function () {
              return {
                  showIcon: false,
                  status: ''
              };
          }
      },
      /**
       * 是否启用下拉刷新的交互
       */
      pulldownUI: {
          type: Boolean,
          default: false
      },
      /**
       * 是否启用上拉加载的交互
       */
      pullupUI: {
          type: Boolean,
          default: false
      }
    },
    data() {
        return {
            loadingConnecting: false,
            pulldownTip: {
                text: '下拉刷新',     // 松开立即刷新
                rotate: '',    // icon-rotate
                status: false //刷新开关
            },
            pullupTip: {
                text: '上拉刷新',     // 松开立即刷新
                rotate: '',    // icon-rotate
                status: false //刷新开关
            },
        }
    },
    mounted() {
      setTimeout(() => {
        this._initScroll()
      }, 20)
    },
    methods: {
      _initScroll() {
        if (!this.$refs.wrapper) {
          return
        }
        this.scroll = new BScroll(this.$refs.wrapper, {
          probeType: this.probeType,
          click: this.click,
          scrollX: this.scrollX,
          scrollY: this.scrollY
        })

        // 是否派发滚动事件
        if (this.listenScroll || this.pulldown || this.pullup) {
            let me = this;
            this.scroll.on('scroll', (pos) => {
                if (this.listenScroll) {
                    me.$emit('scroll', pos);
                }
                if (this.pulldown) {
                    // 下拉动作
                    if (pos.y > 50) {
                        this.pulldownTip = {
                            text: '松开立即刷新',
                            rotate: 'icon-rotate',
                            status:true
                        }
                    } else {
                        this.pulldownTip = {
                            text: '下拉刷新',     // 松开立即刷新
                            rotate: '',    // icon-rotate
                            status:false
                        }
                    }
                }

                if (this.pullup) {
                  if (pos.y < (this.scroll.maxScrollY-10)) {
                    this.pullupTip = {
                      text: '松开立即刷新',
                      rotate: 'icon-rotate',
                      status:true
                    }
                  }else{
                    this.pullupTip = {
                      text: '上拉刷新',
                      rotate: 'icon-rotate',
                      status:false
                    }
                  }
                }
            })
        }
        // if (this.pullup) {
        //   this.scroll.on('scrollEnd', () => {
        //     if (this.scroll.y <= (this.scroll.maxScrollY + 50)) {
        //       this.$emit('scrollToEnd',this.pullupTip)
        //     }
        //   })
        // }
         this.scroll.on('scrollEnd', () => {
            if (this.scroll.y <= (this.scroll.maxScrollY + 50)) {
              this.$emit('scrollToEnd',this.pullupTip)
            }
          })
        // 是否派发顶部下拉事件，用于下拉刷新 
        if (this.pulldown) {
          this.scroll.on('touchend', (pos) => { 
             // 下拉动作 
            if (pos.y > 50) {
               setTimeout(() => {
                  // 重置提示信息
                  this.pulldownTip = {
                      text: '下拉刷新',     // 松开立即刷新
                      rotate: '',    // icon-rotate
                  }
              },300);
              this.$emit('pulldown')
            }
          }) 
        }

        if (this.beforeScroll) {
          this.scroll.on('beforeScrollStart', () => {
            this.$emit('beforeScroll')
          })
        }
      },
      disable() {
        this.scroll && this.scroll.disable()
      },
      enable() {
        this.scroll && this.scroll.enable()
      },
      refresh() {
        this.scroll && this.scroll.refresh()
      },
      scrollTo() {
        this.scroll && this.scroll.scrollTo.apply(this.scroll, arguments)
      },
      scrollToElement() {
        this.scroll && this.scroll.scrollToElement.apply(this.scroll, arguments)
      }
    },
    watch: {
      data() {
         // 监听数据的变化，延时refreshDelay时间后调用refresh方法重新计算，保证滚动效果正常
        setTimeout(() => {
          this.refresh()
        }, this.refreshDelay)
      }
    }
  }
</script>



