<template>
    <div class="main_panel">
        <v-balance></v-balance>
        <!--充值金额 选择 start-->
        <div class="mid-choose">
            <div class="choose-title">快速充值</div>
            <div class="choose-fee">
                <ul>
                    <li v-for="(fee,index) in chooseFee" ><span :val="index+1" class="comm_bg" @click="selectedFee(fee,index+1)" :class="{feeAct:fee == curAct}">{{fee}}</span></li>
                </ul>
            </div>
        </div>
        <!--充值金额 选择 end-->
        <div class="btn-box">
            <div class="submit-btn " :class="{act3:actSubmitBtn}" @click="submitTopup(3)">支付宝(送1元 )</div>
            <div class="submit-btn " :class="{act2:actSubmitBtn}" @click="submitTopup(2)">QQ钱包(推荐)</div>
            <div class="submit-btn " :class="{act:actSubmitBtn}" @click="submitTopup(1)">微信充值</div>
        </div>
        <div class="desc">
            <ul>
                <li>充值由第三方收付款平台提供服务,充值秒到账;</li>
                <li>活动期间支付宝充值送1元现金红包;</li>
                <li>其他充值问题请进群联系管员理客服,QQ群:210099865;</li>
            </ul>
        </div>

        <v-prompt v-show="isShowPrompt" :is-show="isShowPrompt" @okfn="promptOkFn">
            <span slot="header">信息提示 </span>
            <span slot="body">{{promptMsg}}</span>
        </v-prompt>
    </div>
</template>

<script>
import HeadBalance from '@/components/common/HeadBalance';
import Prompt from '@/components/common/Prompt';
import {queryBalance} from '../request/api';
export default {
    name: 'Home',
    components: {
        'v-balance': HeadBalance,
        'v-prompt': Prompt,
    },
  data () {
    return {
        curAct: '0',
        curIndex: '0',
        chooseFee: [11, 22, 50, 100, 200, 500],
        balance: 0,
        actSubmitBtn: false,
        isShowPrompt: false,
        promptMsg: '加载中...'
    };
  },
  created () {
      queryBalance().then(data => {
          // NProgress.done();
          let { msg, code, result } = data
          if (code === 200) {
                this.balance = result.balance;
          } else {
              console.log(msg);
          }
      });
  },
    methods: {
        selectedFee(cur,index) {
            this.curAct = cur;
            this.curIndex = index;
            this.actSubmitBtn = true;
        },
        submitTopup(type) {
            if (this.curAct == 0) {
                //alert('请选择充值金额');
                return;
            }
      /*      this.promptMsg = '冲值接口升级中，请稍后...';
            this.isShowPrompt = true;
            return;*/
            // 命名的路由
            console.log(this.curIndex);
            // 如果使用 paramsw传参只能使用name，不能使用path
            // 如果使用 paramsw传参只能使用name，不能使用path
            if(type == 1){
                this.$router.push({name: 'wxtopup', params: { num: this.curIndex }});
            }else if(type == 2){
                this.$router.push({name: 'qqtopup', params: { num: this.curIndex }});
            }else if(type == 3){
                this.$router.push({name: 'alitopup', params: { num: this.curIndex }});
            }
        },
        promptOkFn (msg) {
            this.isShowPrompt = false;
        },
    }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    .main_panel{ padding: 20px}
    .head-balance{
        height: 70px;

    }
    .head-balance .top-title{
        padding-top: 6px;
        height: 25px;
        line-height: 25px;
        font-size: 12px;
        color: #929090;
    }
    .head-balance .bottom-fee{
        line-height: 25px;
        color: #f4733b;
        font-size: 24px;
    }

    .mid-choose{
        padding-top: 32px;
    }
    .choose-title{
        font-size: 14px;
        color: #929090;
        text-align: left;
    }
    .choose-fee ul{
        display: flex;
        flex-flow: row wrap;
        padding-top: 5px;
    }
    .choose-fee li{
        width: 33.3%;
        height: 60px;
        padding: 5px;
        box-sizing: border-box;
    }
    .choose-fee li span{
        width: 100%;
        display: block;
        height: 50px;
        line-height: 50px;
        box-sizing: border-box;
        font-size: 20px;
        color: #c2c0c0;

    }
    .choose-fee li span.feeAct{
        color: #fff;
        background-color: #f4733b;
    }
    .btn-box{
        padding-top: 30px;
    }
    .btn-box .submit-btn{
        margin-bottom: 5px;
        height: 60px;
        font-size: 22px;
        background-color: #fff;
        line-height: 60px;
        color: #c2c0c0;
    }
    .btn-box .submit-btn.act{
        background-color: #43ac3f;
        color: #fff;
    }
    .btn-box .submit-btn.act2{
        background-color: #46a7ec;
        color: #fff;
    }
    .btn-box .submit-btn.act3{
        background-color: #f57403;
        color: #fff;
    }

    .desc{
        text-align: left;
        padding-top: 20px
    }
    .desc li{
        line-height: 22px;
        color: #929090;
        font-size: 14px;
    }
</style>



