<template>
<div>
    <page title="公告详情">
        <div class="scroll comm_bg">
          <div>
          <h1>{{res.title}}</h1>
          <div class="tiem">{{res.update_time}}</div>
          <div class="content">
            {{res.content}}
          </div>
          </div>
        </div>
    </page>
</div>
</template>

<script>
import page from '@/components/common/page';
import loading from "@/components/loading/loading";
import scroll from "@/components/common/scroll";
import {noticedetails} from '../request/api';
export default {
data () {
    return {
      res:{}
    }
},
  components: {
      page,
          scroll,
    loading
  },
  created () {
      if(this.$route.query.id){
        this.query({ id: this.$route.query.id});
      }
  },
    methods: {
    query(param) {
      if (!typeof param == "object") {
        param = {};
      }
      noticedetails(param).then(data => {
        let { msg, code, result } = data;
        if (code === 200) {
          this.res = result.list[0]
        } else {
          console.log(msg);
        }
      });
    }
  }
}
</script>

<style lang="stylus" scoped>
.scroll {
    height 76vh;
    margin 20px;
    overflow-y:auto;
}
li{
 color #346c91
 padding 10px
 box-sizing border-box
 border-bottom 1px solid #346c91
 overflow hidden
}
h1{
    font-size 22px
    padding 10px
    color #346c91
  line-height 24px
    
}

.tiem{
  font-size 12px
  color #346c91
  border-bottom 1px solid #346c91
  padding-bottom 5px
}
.content{
 font-size 14px
 text-align left 
 color #346c91
 line-height 22px
 padding 10px;
     letter-spacing:2px;

}
</style>



