<template>
    <transition name="prompt-fade">
    <div class="prompts">
        <div class="prompt" >
            <div class="prompt-content" :class="{act:show}">
                <header class="prompt-header" slot="header">
                    <h1 class="prompt-title">
                        <slot name="header"></slot>
                    </h1>
                </header>
                <div class="prompt-body">
                    <slot name="body"></slot>
                </div>
                <footer class="prompt-footer" slot="footer">
                    <button @click="sumbitBtn" >确认</button>
                </footer>

            </div>
        </div>
        <div class="prompt-overlay" ></div>
    </div>
    </transition>
</template>

<script>
export default {
  name: 'Prompt',
    props: {
        yejieMsg: {
            type: String,
            default: ''
        },
        isShow: {
            type: Boolean,
            default: false
        }
    },
  data () {
    return {
        show: false
    };
  },
    created () {
        this.show = this.isShow;
    },
  methods: {
      close: function() {
          this.show = false;
      },
      shows: function() {
          this.show = true;
      },
      sumbitBtn() {
          this.$emit('okfn', 'msg');
      }
  },
    watch: {
        'isShow' : function (newValue, oldValue) {
            var self = this;
            setTimeout(function(){
                self.show =  newValue;
            },50);
        }
    }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    /**
 弹出层
  **/
    .prompt {
        width: 85%;
        position: fixed;
        left: 50%;
        top: 50%;
        min-height: 200px;
        transform: translate(-50%,-50%);
        z-index: 2000;
        visibility: visible;
        backface-visibility: hidden;
    }
    .prompt-content {
        padding: 20px;
        border-radius: 4px;
        background: #fff;
        overflow: hidden;
        box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1);
        transition: .5s ease-in-out;
        opacity: 0;

    }
    .prompt-content.act {
        opacity: 1;
    }
    .prompt-overlay {
        content: "";
        position: fixed;
        visibility: hidden;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        z-index: 1000;
        opacity: 0;
        background: rgba(0, 0, 0, 0.5);
        transition: all .6s;
        opacity: 1;
        visibility: visible;
    }
    .prompt-header{
        text-align: center;
        height: 40px;
        padding-bottom: 20px;}
    .prompt-header h1{
        font-weight: bold;
        font-size: 22px;
    }
    .prompt-body{padding-bottom: 20px; line-height: 22px;}

    .prompt-footer button{ height: 50px; width: 100%;background-color: #43ac3f;border-radius: 4px; color: #fff; font-size: 22px}
</style>



