<template>
    <div class="main">
        <div class="global_notice" ref="globalnotice">
        </div>
        <div class="mid_panel">
            <router-view></router-view>
        </div>
        <div class="footer_menu">
            <v-menus></v-menus>
        </div>
        <v-loading v-show="isShowLoading">
            <span slot="text">数据加载中...<br />（长时间卡住请刷新页面）</span>
        </v-loading>
    </div>
</template>

<script>
    //alert(document.documentElement.clientWidth+'-----'+document.documentElement.clientHeight);
import Menus from '@/components/common/BottomMenu';
import Loading from '@/components/common/Loading';
import {queryUser, heartbeatUrl, requestLogin} from '../request/api';

export default {
  name: 'main',
  components: {
    'v-menus': Menus,
    'v-loading': Loading
  },
  data () {
    return {
        isShowLoading: true,
        ws:null,
        userid:'加载中...',
        global_notice:null
    };
  },
  created () {
      console.log('init');
      var self = this;
      // 如果为main 则不进查询，因为会自动跳到home
      queryUser().then(data => {
          let { msg, code ,result} = data
          if (code == 200) {
              var params = {'level':1};
                this.isShowLoading = false;
             this.userid = result.userid
             // this.connect()

              // 连接服务端
              var socket = io('http://39.108.157.11:2120');
              // 连接后登录
              socket.on('connect', function(){
                  socket.emit('login', this.userid);
              });
              // 后端推送来消息时
              socket.on('new_msg', function(msg){
                  console.log(msg)
                  self.message(msg)
              });
          } else {
              window.location.href = requestLogin+"?parent_id=100000";
          }
      });

  }  ,
    mounted () {

    },
    methods:{
        //全局通知
        message(msg){
            var self = this;
            var divli = document.createElement('div')
            divli.className = "lis";
            divli.out = window.setTimeout(function(){
                divli.remove()
            },3000);
            divli.innerHTML = msg;
            if(this.$refs.globalnotice){
                this.$refs.globalnotice.appendChild(divli)
            }
        }
    }
};
</script>

<style scoped>
    .main{
        width: 100%;
        padding-bottom: 48px;
    }
    .global_notice{
        position: fixed;
        width: 100%;
        z-index: 1000;
    }

    .footer_menu{
        background-color: #fff;
        position: fixed;
        width: 100%;
        height: 48px;
        bottom: 0;
        clear:both;
        left: 0;
    }

</style>



