<template>
    <div class="main_panel">
        <div class="header-banner" @click="isShowPopup = true">
            <div class="user-id">ID:{{userid}}</div>
        </div>
        <div class="account-panel">
            <div class="items left-balance">
                <div class="balance-panel comm_bg">
                    <router-link to="/mycenter">
                    <div class="balance-title">账户余额（元）<br />
                        <span class="fee">{{balance}}</span>
                    </div>
                    </router-link>
                </div>
            </div>
            <div class="items up_with">
                <div class="topup btn2"><router-link to="/topup">充值</router-link></div>
                <div class="withdraw btn1"><router-link to="/withdraw">提现</router-link></div>
            </div>
        </div>

        <div class="mid-nav">
            <div class="navs-panel comm_bg">
                <div class="nav-item"><router-link to="macthrule"><img src="../assets/img/home/rules.png" /></router-link></div>
                <div class="nav-item"><div class="new-msg">1</div><router-link to="/myqrcodeurl"><img src="../assets/img/home/agency.png" /></router-link></div>
                <div class="nav-item"><router-link to="/followqrcode"><img src="../assets/img/home/follow_we.png" /></router-link></div>
            </div>
        </div>

        <div class="start-panel">
            <div class="start-btn btn1"><router-link to="/match">开始吃鸡</router-link></div>
        </div>
        <div class="bottom_ico"><span><img src="../assets/img/home/wx_authorize.png" /></span>微信认证 - H5精品应用</div>
        <div class="bottom2_ico"><a href="/wxreport/">举报此应用</a></div>
        <div v-if="isShowRedPack" class="dialog-overlay">
            <div  class="reg-bg-box">
                <div class="content">
                    <div class="vercode-box">
                        <div class="head-title"> 防止刷单，请先点击下方 “ {{verCodeName}} ” 图标</div>
                        <div class="code-box">
                            <div class="items" v-for="item in codeList"  :class="{act:item == codeAct}" @click="saveVerCode(item)"><img :src="'service/home/captcha/imgcaptche?img='+item" /></div>
                        </div>
                        <div class="bottom-btn" @click="submitRedPack()"></div>
                    </div>
                </div>
            </div>
        </div>

        <v-prompt v-show="isShowPrompt" :is-show="isShowPrompt" @okfn="promptOkFn">
            <span slot="header">信息提示 </span>
            <span slot="body">{{promptMsg}}</span>
        </v-prompt>

        <v-popup v-show="isShowPopup" :is-show="isShowPopup" @okfn="popupOkFn">
            <div slot="body">
                <div class="title">关注我们，有机会获取红包</div>
                <div class="qrcode"><img src="/service/Public/follow_qrcode.jpg" /></div>
                <div class="pop-btn">
                    <button @click="isShowPopup = false">确认关闭</button>
                </div>
            </div>
        </v-popup>
    </div>

</template>

<script>
import Prompt from '@/components/common/Prompt';
import Popup from '@/components/common/Popup';
import {queryBalance,newUserRedpacketact,createCaptche,recommendUser,submitNewUserAct,queryNewRed} from '../request/api';
export default {
    name: 'Home',
    components: {
        'v-prompt': Prompt,
        'v-popup': Popup
    },
  data () {
    return {
        redFee: 0,
        isShowRedPack: false,
        balance: '加载中...',
        userid: '加载中...',
        verCodeName:'加载中...',
        codeList:[],
        curVercode:null,
        codeAct:null,
        isShowPrompt:false,
        promptMsg:'加载中...',
        isShowPopup:false,
        isPromptEnd:false,
        isFristr:true
    };
  },
  created () {
      queryBalance().then(data => {
          let { msg, code, result } = data
          if (code == 200) {
              var bal = parseFloat(result.balance);
              var redPacket = parseFloat(result.red_packet);
              this.balance = (bal + redPacket).toFixed(2);
              this.userid = result.id;
              this.querynewusermoney();//用于领取红包的链接 ，访问链接就会领
          } else {
              this.isShowPrompt =true;
              this.promptMsg ='登录已超时，重刷新页面';
              return;
          }
      });
  },
    methods:{
        popupOkFn(res){
            this.isShowPopup = false
        },
        queryRedpacketAct() {
            newUserRedpacketact().then(data => {
                let { msg, code, result } = data
                if (code == 200) {
                    this.isShowDialog = true;
                    this.redFee = (result.fee / 100).toFixed(2);
                    this.balance = (parseFloat(this.balance) + (result.fee / 100)).toFixed(2);
                    // console.log(this.redFee, this.balance);

                } else {
                    console.log(msg);
                }
            });
        },
        //查询上级推广奖励
        querynewusermoney(){

            //查询红包是否领取
            queryNewRed().then(data => {
                let { msg, code, result } = data
                if (code == 200) {
                    this.isShowRedPack = true;
                    //console.log('new user',result);
                    this.createCaptcha();
                }else{
                    var redp = window.localStorage.red;
                    if(!redp){
                        this.isShowPopup = true;
                        window.localStorage.red = true;
                    }

                }
            });
           /* recommendUser().then(data => {
                let { msg, code, result } = data
                if (code == 200) {
                    //this.isShowRedPack = true;
                    //console.log('new user',result);
                    this.createCaptcha();
                }else{
                    this.isShowPopup = true;
                }
            });*/
        },
        /**
         * 创建验证码
         */
        createCaptcha(){
            createCaptche().then(data => {
                let { msg, code, result } = data
                if (code == 200) {
                    this.verCodeName = result.name;
                    this.codeList = result.list;
                } else {
                    console.log(msg);
                }
            });
        },
        //保存用户选择验证码
        saveVerCode(item){
            this.codeAct = item;
            this.curVercode = item;
        },
        //提交红包确认领奖
        submitRedPack(){
            if(!this.curVercode){
                this.isShowPrompt =true;
                this.promptMsg ='请先选择上方图标';
                return;
            }
            var params = {code:this.curVercode};
            submitNewUserAct(params).then(data => {
                let { msg, code,msgcode, result } = data
                if (code == 200) {
                    this.isShowRedPack = false;
                    this.queryRedpacketAct();
                    this.isShowPrompt =true;
                    this.promptMsg ='你的推荐人已获得现金奖励（可提现）！你的5元红包已经到帐！';
                    this.isPromptEnd = true;
                } else if(msgcode == 'code_error') {
                    this.isShowPrompt =true;
                    this.promptMsg ='图标选择不正确，请重新选择';
                    this.createCaptcha();
                }else if(msgcode =='parentid_is_null'){
                    this.isShowRedPack = false;
                    this.isShowPrompt =true;
                    this.promptMsg =  '把你的二维码推荐给好友得现金（可提现）！你的5元红包已经帐户！';
                    this.isPromptEnd = true;
                    this.queryRedpacketAct();
                }else{
                    this.isShowRedPack = false;
                    this.isShowPrompt =true;
                    this.promptMsg ='领取错误，请联系管理员 '+msg;

                }
            });
        },
        promptOkFn(){
            this.isShowPrompt =false;
            if(this.isPromptEnd && this.isFristr){
                this.isShowPopup = true;
                this.isFristr = false
            }

        }
    }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
