    .start-panel{
        padding: 10px 20px 0 20px;
    }
    .start-btn{
        height:70px;
        display: flex;
        background: -webkit-gradient(linear,0 0,0 100%,from(#df9e00),to(#cd9200)); ;

    }
    .start-btn a{ height:inherit;
        width: 100%;
        display: flex;
        font-size: 24px;
