        text-shadow: 1px 1px 1px #000;
    }
    .bottom_ico{ padding-top: 10px; font-size: 12px; color: #ccc}
    .bottom_ico span{padding-right: 4px}
    .bottom_ico img{ vertical-align: bottom; width: 16px}

    .bottom2_ico{
        padding-top:10px;
        font-size: 14px;

    }
    .bottom2_ico a{
        color: #027db5;
    }

    /**
    *红包样式
     */

    .dialog-overlay {
        content: "";
        position: fixed;
        visibility: hidden;
        top: 0;
