<template>
    <div class="main_panel">
        <div class="headers comm_bg">
            <div class="topup">充值:<span>{{topup}}</span> 元</div>
            <div class="withdraw">提现:<span>{{withdraw}}</span> 元</div>
        </div>
        <div class="mid_panel">
            <scroll @scrollToEnd="scrollToEnd" :data="listData" class="scroll comm_bg">
            <ul class="tw-list " ref="listbox">
                <li :class="{'topup':item.type == 1,'withdraw':item.type == 2}"  v-for="(item,index) in listData" :key="index">
                    <div class="li-head">
                        <div class="left-box">
                            <div class="title">
                                <span v-if="item.type == 1">充值</span>
                                <span v-if="item.type == 2">提现</span>
                            </div>
                            <div class="time">{{item.create_time}}</div>
                        </div>
                        <div class="right-box">
                            <div class="fee">{{item.fee/100}}元</div>
                            <div class="state">
                                <span v-if="item.state ==1">交易中-发起充值请求</span>
                                <span v-if="item.state ==2">交易成功</span>
                                <span v-if="item.state ==3">交易失败</span>
                            </div>
                        </div>
                    </div>
                    <div class="order-on">
                        交易号:{{item.order_on}}
                    </div>
                </li>
            </ul>
              <loading :imgshow="!noresults" :title="loadingtitle"></loading>
          </scroll>
        </div>
    </div>
</template>

<script>
import { topupWithdraw } from "../request/api";
import loading from "@/components/loading/loading";
import scroll from "@/components/common/scroll";
export default {
  name: "TopupWithdraw",
  data() {
    return {
      listData: [],
      topup: "加载中...",
      withdraw: "加载中...",
      loading: true,
      noresults: false,
      loadingtitle: "正在加载",
      pagenum: 1
    };
  },
  components: {
    scroll,
    loading
  },
  created() {
    this.query({ cur: this.pagenum });
  },
  methods: {
    query(param) {
      if (!typeof param == "object") {
        param = {};
      }
      topupWithdraw(param).then(data => {
        let { msg, code, result } = data;
        if (code === 200) {
          if (result.page.list && result.page.list.length > 0) {
            if (result.page.list.length < 19) {
              this.noresults = true;
              this.loadingtitle = "没有更多了";
            }
          } else {
            this.noresults = true;
            this.loadingtitle = "没有更多了";
          }
          this.withdraw = result.page.withdraw;
          this.topup = result.page.topup;
          this.listData.push.apply(this.listData, result.page.list);
        } else {
          console.log(msg);
        }
      });
    },
    scrollToEnd() {
      if (this.loadingtitle !== "没有更多了") {
        this.query({ cur: ++this.pagenum });
      }
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.main_panel {
  padding: 20px;
  color: #027db5;
}
.headers {
  margin-bottom: 20px;
  display: flex;
  font-size: 14px;
  line-height: 70px;
}
.headers .topup {
  flex: 1;
}
.headers .topup span {
  color: #43ac3f;
  font-size: 22px;
}
.headers .withdraw {
  flex: 1;
}
.headers .withdraw span {
  color: #f4733b;
  font-size: 22px;
}

.tw-list li {
  height: 46px;
  padding: 5px 10px;
  border-bottom: solid 1px #393c45;
}
.tw-list li:last-child {
  border-bottom: 0;
}
.tw-list .li-head {
  display: flex;
}
.tw-list li .left-box {
  text-align: left;
  flex: 1;
}
.tw-list li .title {
  line-height: 24px;
}
.tw-list li .time {
  font-size: 12px;
  color: #7e8c8d;
  line-height: 16px;
}
.tw-list li .right-box {
  line-height: 22px;
  flex: 1;
}
.tw-list li.withdraw .fee {
  color: #f4733b;
  text-align: right;
}
.tw-list li.topup .fee {
  color: #43ac3f;
  text-align: right;
}
.tw-list li .state {
  font-size: 11px;
  color: #7e8c8d;
  text-align: right;
}
.tw-list .order-on {
  line-height: 30px;
  font-size: 12px;
  color: #adacac;
  display: none;
}


.scroll{
  height: 75vh;
  overflow: hidden;
}

@media (device-height:568px) and (-webkit-min-device-pixel-ratio:2){/* 兼容iphone5 */
.scroll{
  height: 69vh;
  }
}

@media (device-height:667px) and (-webkit-min-device-pixel-ratio:2){/* 兼容iphone6 */
.scroll{
  height: 73vh;
  }
}
</style>



