<template>
    <div class="main_panel">
        <v-balance @okfn="balanceCallFn"></v-balance>

        <div class="headers ">
            <div class="topup">可用余额:<span>{{balance}}</span> 元</div>
            <div class="cwithdraw">红包余额:<span>{{redPacket}}</span> 元</div>
        </div>
        <div class="mid_panel">
                      <scroll @scrollToEnd="scrollToEnd" :data="listData" class="scroll comm_bg">
            <ul class="tw-list " ref="listbox">
                <li class="topup" v-for="(item,index) in listData" :key="index">
                    <div class="left-box">
                        <div class="title">{{item.mark}}</div>
                        <div class="time">{{item.create_time}}</div>
                    </div>
                    <div class="right-box" :class="{'plus':item.inex == 2||item.inex == 3,'minus':item.inex == 1||item.inex == 4}"> <span v-if="item.inex ==1||item.inex == 4">-</span><span v-else-if="item.inex == 2||item.inex == 3">+</span> {{(item.balance/100).toFixed(2)}}元</div>
                </li>
            </ul>
                        <loading :imgshow="!noresults" :title="loadingtitle"></loading>
          </scroll>
        </div>
    </div>
</template>

<script>
import HeadBalance from "@/components/common/HeadBalance";
import loading from "@/components/loading/loading";
import scroll from "@/components/common/scroll";
import { transfersHistory } from "../request/api";
export default {
  name: "TransfersHistory",
  components: {
    "v-balance": HeadBalance,
    scroll,
    loading
  },
  data() {
    return {
     balance: "加载中...",
      redPacket: "加载中...",
      listData: [],
      loading: true,
      noresults: false,
      loadingtitle: "正在加载",
      pagenum: 1
    };
  },
  created() {
    this.query({ cur: this.pagenum });
  },
  methods: {
    balanceCallFn(res) {
      var bal = parseFloat(res.balance);
      var redPacket = parseFloat(res.red_packet);
      this.balance = bal.toFixed(2);
      this.redPacket = redPacket.toFixed(2);
    },
    query(param) {
      if (!typeof param == "object") {
        param = {};
      }
      transfersHistory(param).then(data => {
        let { msg, code, result } = data;
        if (code === 200) {
          if (result.page.list && result.page.list.length > 0) {
            if (result.page.list.length < 19) {
              this.noresults = true;
              this.loadingtitle = "没有更多了";
            }
          } else {
            this.noresults = true;
            this.loadingtitle = "没有更多了";
          }
          this.listData.push.apply(this.listData, result.page.list);
        } else {
          console.log(msg);
        }
      });
    },
    scrollToEnd() {
      if (this.loadingtitle !== "没有更多了") {
        this.query({ cur: ++this.pagenum });
      }
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.main_panel {
  padding: 20px;
  color: #027db5;
}
.mid_panel {
}
.headers {
  display: flex;
  font-size: 14px;
  line-height: 40px;
  font-size: 12px;
  color: #929090;
}
.headers .topup {
  flex: 1;
}
.headers .cwithdraw {
  flex: 1;
}

.tw-list li {
  display: flex;
  height: 40px;
  padding: 5px 10px;
  border-bottom: solid 1px #393c45;
}
.tw-list li .plus {
  color: #43ac3f;
}
.tw-list li .minus {
  color: #f4733b;
}

.tw-list li:last-child {
  border-bottom: 0;
}

.tw-list li .left-box {
  text-align: left;
  flex: 2;
}
.tw-list li .title {
  line-height: 24px;
  font-size: 12px;
}
.tw-list li .time {
  font-size: 12px;
  color: #7e8c8d;
}
.tw-list li .right-box {
  flex: 1;
  text-align: right;
  line-height: 40px;
}
.scroll {
  height: 74vh;
  overflow: hidden;
}

@media (device-height:568px) and (-webkit-min-device-pixel-ratio:2){/* 兼容iphone5 */
.scroll{
  height: 66vh;
  }
}

@media (device-height:667px) and (-webkit-min-device-pixel-ratio:2){/* 兼容iphone6 */
.scroll{
  height: 71vh;
  }
}
</style>



