import Vue from 'vue';
import Router from 'vue-router';
import Main from '@/views/Main';
import Home from '@/views/Home';
import Match from '@/views/Match';
import Ranking from '@/views/Ranking';
import Mycenter from '@/views/Mycenter';
import Topup from '@/views/Topup';
import WxTopup from '@/views/WxTopup';
import QqTopup from '@/views/QqTopup';
import AliTopup from '@/views/AliTopup';
import Withdraw from '@/views/Withdraw';
import WithdrawForwx from '@/views/WithdrawForwx';
import WithdrawForali from '@/views/WithdrawForali';
import TopupWithdrawHistory from '@/views/TopupWithdrawHistory';
import MacthHistory from '@/views/MacthHistory';
import TransfersHistory from '@/views/TransfersHistory';
import MyRecommend from '@/views/MyRecommend';
import AgencyHistory from '@/views/AgencyHistory';
import TodayIncome from '@/views/TodayIncome';
import MyQrcode from '@/views/MyQrcode';
import FollowQrcode from '@/views/FollowQrcode';
import MacthRule from '@/views/MacthRule';
import noticelist from '@/views/noticelist';
import noticedetails from '@/views/noticedetails';
Vue.use(Router)
const router = new Router({
  routes: [
      {
          path: '/',
          name: 'Main',
          component: Main,
          meta: {
              isHome: true,
              requireAuth: true  // 添加该字段，表示进入这个路由是需要登录的
          },
          children: [
              { path: '/home', component: Home, meta: {requireAuth: true}, name: 'home' },
              { path: '/match', component: Match, meta: {requireAuth: true}, name: 'match', hidden: true },
              { path: '/ranking', component: Ranking, meta: {requireAuth: true}, name: 'ranking', hidden: true },
              { path: '/mycenter', component: Mycenter, meta: {requireAuth: true}, name: 'mycenter', hidden: true },
              { path: '/topup', component: Topup, meta: {requireAuth: true}, name: 'topup', hidden: true },
              { path: '/wxtopup', component: WxTopup, meta: {requireAuth: true}, name: 'wxtopup', hidden: true },
              { path: '/qqtopup', component: QqTopup, meta: {requireAuth: true}, name: 'qqtopup', hidden: true },
              { path: '/alitopup', component: AliTopup, meta: {requireAuth: true}, name: 'alitopup', hidden: true },
              { path: '/withdraw', component: Withdraw, meta: {requireAuth: true}, name: 'withdraw', hidden: true },
              { path: '/withdrawforwx', component: WithdrawForwx, meta: {requireAuth: true}, name: 'withdrawforwx', hidden: true },
              { path: '/withdrawforali', component: WithdrawForali, meta: {requireAuth: true}, name: 'withdrawforali', hidden: true },
              { path: '/twshistory', component: TopupWithdrawHistory, meta: {requireAuth: true}, name: 'topupwithdrawhistory', hidden: true },
              { path: '/matchshistory', component: MacthHistory, meta: {requireAuth: true}, name: 'matchshistory', hidden: true },
              { path: '/transfershistory', component: TransfersHistory, meta: {requireAuth: true}, name: 'transfershistory', hidden: true },
              { path: '/myrecommend', component: MyRecommend, meta: {requireAuth: true}, name: 'myrecommend', hidden: true },
              { path: '/agencyhistory/:type', component: AgencyHistory, meta: {requireAuth: true}, name: 'agencyhistory', hidden: true },
              { path: '/todayincome', component: TodayIncome, meta: {requireAuth: true}, name: 'todayincome', hidden: true },
              { path: '/myqrcodeurl', component: MyQrcode, meta: {requireAuth: true}, name: 'myqrcodeurl', hidden: true },
              { path: '/followqrcode', component: FollowQrcode, meta: {requireAuth: true}, name: 'followqrcode', hidden: true },
              { path: '/macthrule', component: MacthRule, meta: {requireAuth: true}, name: 'macthrule', hidden: true },
              { path: '/noticelist', component: noticelist, meta: {requireAuth: true}, name: 'noticelist', hidden: true },
              { path: '/noticedetails', component: noticedetails, meta: {requireAuth: true}, name: 'noticedetails', hidden: true }

          ]
      }
  ],
    linkActiveClass: 'active' // 标签点击效果
});
router.beforeEach((to, from, next) => {
    if (to.meta.isHome) {
        if (to.path === '/') {  // 判断是否为首页
            next({
                path: '/home',
                query: { id: to.query.id }
            });
        }else{
            next();
        }
    }else{
        next();
    }

});
export default router;



