<template>
    <div class="main_panel">
        <div class="headers-box">
            最近100扫雷记录
        </div>
        <div class="mid_panel">
          <scroll @scrollToEnd="scrollToEnd" :data="listData" class="scroll">
            <ul class="tw-list" ref="listbox">
                <li class=" comm_bg" :class="{roll:isRoll}"  v-for="(item,index) in listData" :key="index">
                    <div class="his-header">
                        <div class="left-title">
                            <span v-if="item.type ==1">单雷区-1中{{item.level}}</span>

                            <span v-if="item.type ==3">多雷区</span>
                        </div>
                        <div class="left-right">
                            <span class="win" v-if="item.match_result ==1">赢+{{(item.match_fee * item.odds)/10000}}</span>
                            <span class="lost"v-else-if="item.match_result ==2"> 输 -{{item.match_fee/100}}</span>
                            <span class="tie" v-else> 彩蛋 +{{(item.match_fee * item.odds)/10000}}</span>
                        </div>
                    </div>
                    <div class="mid-list">
                        <div class="lis">
                            <div class="lis-title">微信订单</div>
                            <div class="lis-value">{{item.order_id}}</div>
                        </div>
                        <div class="lis">
                            <div class="lis-title">吃鸡金额</div>
                            <div class="lis-value">{{item.match_fee/100}}元</div>
                        </div>
                        <div class="lis">
                            <div class="lis-title">吃鸡号码</div>

                            <div  class="lis-value" >
                                {{item.user_in_val.split('').join(',')}}
                            </div>
                        </div>
                        <div class="lis">
                            <div class="lis-title">订单通知</div>
                            <div class="lis-value">{{item.single_order_fee/100}}元</div>
                        </div>
                        <div class="lis">
                            <div class="lis-title">手续费</div>
                            <div class="lis-value">{{item.poundage_fee/100}}元</div>
                        </div>
                        <div class="lis">
                            <div class="lis-title">吃鸡时间</div>
                            <div class="lis-value">{{item.create_time}}</div>
                        </div>
                    </div>
                </li>
            </ul>
            <loading :imgshow="!noresults" :title="loadingtitle"></loading>
          </scroll>
        </div>
    </div>
</template>

<script>
import loading from "@/components/loading/loading";
import scroll from "@/components/common/scroll";
import { matchHistory } from "../request/api";

export default {
  name: "Mycenter",
  data() {
    return {
      listData:[],
      isRoll: false,
      loading: true,
      noresults: false,
      loadingtitle: "正在加载",
      pagenum:1
    };
  },
  components: {
    scroll,
    loading
  },
  created() {
    this.query({cur:this.pagenum})
  },
  methods: {
    query(param){
       if (!typeof param == "object") {
        param = {}
      }
        matchHistory(param).then(data => {
            let { msg, code, result } = data;
            if (code === 200) {
                if(result.page.list && result.page.list.length > 0) {
                    if(result.page.list.length<19){
                        this.noresults = true
                        this.loadingtitle = "没有更多了"
                    }
                }else{
                    this.noresults = true
                    this.loadingtitle = "没有更多了"
                }
                this.listData.push.apply(this.listData, result.page.list)
            } else {
                console.log(msg);
            }
        });
    },
      scrollToEnd(){
          if(this.loadingtitle !== "没有更多了"){
              this.query({cur:++this.pagenum})
          }
      }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.main_panel {
  padding: 20px;
}
.headers-box {
  color: #929090;
  line-height: 40px;
}

.tw-list li {
  padding: 10px;
  margin-top: 10px;
  transition: margin-top 0.5s;
}
.tw-list li.roll {
  transform: scaleY(0.8);
}
.tw-list li .win {
  color: #43ac3f;
}
.tw-list li .lost {
  color: #f4733b;
}
.tw-list li .tie {
  color: #027db5;
}
.tw-list .his-header {
  display: flex;
}
.tw-list .his-header .left-title {
  flex: 1;
  text-align: left;
  color: #027db5;
}
.tw-list .his-header .left-right {
  flex: 1;
  text-align: right;
}

.mid-list {
  padding-top: 10px;
}
.mid-list .lis {
  display: flex;
  line-height: 22px;
}
.mid-list .lis .lis-title {
  flex: 1;
  text-align: left;
  font-size: 14px;
  color: #929090;
}
.mid-list .lis .lis-value {
  flex: 3;
  font-size: 12px;
  color: #b6b6b6;
  text-align: right;
}

.scroll{
  height: 78vh;
  overflow: hidden;
}

@media (device-height:568px) and (-webkit-min-device-pixel-ratio:2){/* 兼容iphone5 */
.scroll{
  height: 79vh;
  }
}

@media (device-height:667px) and (-webkit-min-device-pixel-ratio:2){/* 兼容iphone6 */
.scroll{
  height: 82vh;
  }
}
</style>



