//公告详情
export const noticedetails = params => { return fromaxios(`${base}/notice/querydetails`,params).then(res => res.data)}


export const fromaxios = (url,params) => {
    return axios({
        url:  url,
        method: 'post',
        data: params,
        transformRequest: [function (data) {
            // Do whatever you want to transform the data
            let ret = []
            for (let it in data) {
                ret.push(encodeURIComponent(it) + '=' + encodeURIComponent(data[it]))
            }
            return ret.join('&')
        }],
        headers: {
            'Content-Type': 'application/x-www-form-urlencoded'
        }
    })
}


