<template>
    <div class="main_panel">
        <v-balance @okfn="balanceCallFn" ref="headbalance"></v-balance>
        <div class="mid-choose">
            <div class="withdraw-title">快速提现</div>
            <div class="withdraw-type">
                <ul>
                    <li class="comm_bg"><div class="icon"><img src="../assets/img/common/wx_pay_type.png" /></div>微信提现<br /><span>微信钱包</span></li>
                </ul>
            </div>
        </div>
        <div class="withdraw-input-box comm_bg">
            <div class="input-title">
                提现金额 <span>提现需1%手续费（最少提现1元）</span>
            </div>
            <div class="input-panel">
                <div class="fee-icon">¥</div>
                <div class="fee-input"><input type="tel" v-model="inputVal" maxlength="3" /></div>
            </div>

            <div class="balances"><ul>
                <li>今日可提现 <span style="color: #f4733b; font-size: 16px">{{withdrawCount}}</span> 次(扫雷满100次则无法提现)</li>
                <li>可提现余额 <span>{{balance}}</span> 元（每日提现 5 次）</li>
                <li>红包余额 <span>{{redPacket}}</span> 元（红包不可提现）</li>
            </ul></div>
        </div>
        <div class="btn-box">
            <div class="submit-btn comm_bg" @click="affirmWithdraw">确认提现</div>
        </div>
        <v-prompt v-show="isShowPrompt" :is-show="isShowPrompt" @okfn="promptOkFn">
            <span slot="header">信息提示 </span>
            <span slot="body">{{promptMsg}}</span>
        </v-prompt>

        <v-loading v-show="isShowLoading">
            <span slot="text">正在提现中...</span>
        </v-loading>
    </div>
</template>

<script>
import HeadBalance from '@/components/common/HeadBalance';
import Prompt from '@/components/common/Prompt';
import Loading from '@/components/common/Loading';
import {queryBalance, reqWithdraw} from '../request/api';
export default {
    name: 'withdraw',
    components: {
        'v-balance': HeadBalance,
        'v-prompt': Prompt,
        'v-loading': Loading
    },
  data () {
    return {
        curAct: '20',
        balance: 0,
        redPacket: 0,
        isShowPrompt: false,
        promptMsg: '加载中...',
        inputVal: '',
        isShowLoading: false,
        enBalance:0,
        withdrawCount:'加载中..'
    };
  },
  created () {

  },
    methods: {
        promptOkFn (msg) {
            this.isShowPrompt = false;
        },
        inputFeeBlur () {
            let bal = Number(this.inputVal);
            if (bal > (this.balance - 0.1)) {
                this.inputVal = Math.floor(this.balance - 0.1);
            }
        },
        affirmWithdraw () {
            var self = this;
            let bal = Number(this.inputVal);
            if (!(bal >= 1 && bal < 10000)) {
                this.promptMsg = '金额超出提现范围，最小1元，最大10000元';
                this.isShowPrompt = true;
                this.inputVal = '';
                return;
            }

            if(this.balance < 1 ){
                this.promptMsg = '帐户余额不足！';
                this.isShowPrompt = true;
                this.inputVal = '';
                return;
            }
            // 手续费
            var poundage =  (this.balance * 100) * 100;
            poundage /= 1000000;
            console.log(poundage);
            poundage -= (poundage * 0.01);
            if (bal > (this.balance - poundage)) {
                this.promptMsg = '金额超出提现范围,最大提现:' + Math.floor(this.balance - poundage)+"元,其中需要扣除("+poundage.toFixed(2)+"手续费)";
                this.isShowPrompt = true;
                this.inputVal = '';
                return;
            }
            var params = {'fee':bal};
            if(this.isShowLoading){
                return;
            }
            this.isShowLoading = true;
            reqWithdraw(params).then(data => {
                this.isShowLoading = false;
                let { msg, msgcode, code, result } = data
                if (code == 200) {
                    this.promptMsg = '提现成功，请查收微信钱包';
                    this.isShowPrompt = true;
                    this.balance =  parseFloat(this.balance - bal).toFixed(2);
                   // console.log(self.$refs.headbalance.balance = this.balance)
                    //self.$refs.headbalance.setbalance(this.balance);
                    self.$refs.headbalance.balance = this.balance
                    this.inputVal = '';

                } else {
                    this.promptMsg = msg;
                    this.isShowPrompt = true;
                }
            });
        },
        balanceCallFn (res) {
            var bal = parseFloat(res.balance);
            var redPacket = parseFloat(res.red_packet);
            this.balance = bal.toFixed(2);
            this.redPacket = redPacket.toFixed(2);
            var matchCount = res.macthcount;

            if(matchCount>=95){
                res.witharwcount += (matchCount - 95);
            }
            this.withdrawCount = 5 - res.witharwcount;
        }
    }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    .main_panel{ padding: 20px}

    .mid-choose{
        padding-top: 32px;
    }
    .withdraw-title{
        font-size: 14px;
        color: #929090;
        text-align: left;
    }
    .withdraw-type ul{
        display: flex;
        flex-flow: row wrap;
        padding-top: 5px;
    }
    .withdraw-type li{
        width: 100%;
        color: #027db5;
        margin-top: 10px;
        text-align: left;
        padding-top: 20px;
        line-height: 16px;
        padding-left: 75px;
        position: relative;
        padding-bottom: 20px;
    }
    .withdraw-type li span{
        color: #c2c0c0;
        font-size: 12px;
    }
    .withdraw-type li .icon{
        position: absolute;
        display: flex;
        justify-content:center;
        align-items:center;
        height: 60px;
        padding-left: 20px;
        left: 5px;
        top: 5px;
    }
    .withdraw-type li .icon img{ width: 40px; height: 40px}

    .withdraw-input-box{
        min-height: 134px;
        margin-top: 10px;
        padding: 20px;
    }
    .withdraw-input-box .input-title{
        line-height: 25px;
        height: 25px;
        text-align: left;
        color: #929090;
    }
    .withdraw-input-box .input-title span{
        float: right;
        color: #f4733b;
        font-size: 12px;
    }
    .input-panel{
        padding-top: 20px;
    }
    .input-panel .fee-icon{
        text-align: left;
        font-size: 28px;
        line-height: 36px;
        color: #929090;
        position: absolute;

    }
    .input-panel .fee-input{
        margin-left: 20px;
        height: 34px;
        border-bottom: solid 1px #eeeeee;
    }
    .input-panel .fee-input input{ padding: 0 10px; box-sizing: content-box; width: 100%;height: 34px; font-size: 28px;color: #fff}

    .balances{
        padding-top: 10px;
    }
    .balances li{
        text-align: left;
        line-height: 22px;
        font-size: 14px;
        color: #929090;
    }
    .btn-box{
        margin-top: 20px;
    }
    .btn-box .submit-btn{
        height: 50px;
        font-size: 22px;
        background-color: #44af35;
        display:block;
        width: 100%;
        line-height: 50px;
        color: #fff;
    }
</style>



