<template>
    <div class="main_panel">
        <div class="head-balance comm_bg">
            <div class="top-title">
               累计总佣金
            </div>
            <div class="bottom-fee">
                {{(totalFee/100).toFixed(2)}}元
            </div>
        </div>
        <div class="mid_panel">
            <ul class="my-list">
                <li class="flex comm_bg"><router-link to="/todayincome"><div class="title">今日收益</div><div class="count">{{(todayFee/100).toFixed(2)}}元</div><i></i></router-link></li>
                <li class="flex comm_bg"><router-link to="/myrecommend"><div class="title">推广总人数</div><div class="count">{{totalCount}}人</div><!--<i></i>--></router-link></li>
                <li class="flex comm_bg"><router-link to="/agencyhistory/1"><div class="title">一级下线({{level1}})</div><div class="count">{{(level[0]/100).toFixed(2)}}元</div><i></i></router-link></li>
                <li class="flex comm_bg"><router-link to="/agencyhistory/2"><div class="title">二级下线({{level2}})</div><div class="count">{{(level[1]/100).toFixed(2)}}元</div><i></i></router-link></li>
                <li class="flex comm_bg"><router-link to="/agencyhistory/3"><div class="title">三级下线({{level3}})</div><div class="count">{{(level[2]/100).toFixed(2)}}元</div><i></i></router-link></li>
                <li class="flex comm_bg"><router-link to="/agencyhistory/4"><div class="title">四级下线({{level4}})</div><div class="count">{{(level[3]/100).toFixed(2)}}元</div><i></i></router-link></li>

            </ul>
        </div>
        <v-loading v-show="isShowLoading">
            <span slot="text">数据加载中...<br />（长时间卡住请刷新页面）</span>
        </v-loading>
    </div>
</template>

<script>
import {queryAgency} from '../request/api';
import Loading from '@/components/common/Loading';
export default {
  name: 'Mycenter',
    components: {
        'v-loading': Loading
    },
  data () {
    return {
        id: 1,
        listData:null,
        totalFee:null,
        todayFee:null,
        level:[0,0,0,0],
        recount:0,
        totalCount:0,
        level1:0,
        level2:0,
        level3:0,
        level4:0,
        isShowLoading: true

    };
  },
  created () {
      var params = {};
      queryAgency(params).then(data => {
          this.isShowLoading = false;
          let { msg, code, result } = data
          if (code === 200) {
              this.totalFee = result.totalfee;
              this.listData = result.list;
              this.recount = result.recount;
              this.level1 = Number(this.recount.level1.count);
              this.level2 = Number(this.recount.level2.count);
              this.level3 = Number(this.recount.level3.count);
              this.level4 = Number(this.recount.level4.count);
              this.totalCount = this.level1 + this.level2 +this.level3 + this.level4;
              var userid = result.userid;
              for(var i = 0;i<result.list.length;i++){
                  this.level[result.list[i].level-1] = result.list[i].fee;
              }
              this.todayFee = result.todayfee;
          } else {
              console.log(msg);
          }
      });

  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    .head-balance{
        height: 70px;
    }
    .head-balance .top-title{
        padding-top: 6px;
        height: 25px;
        line-height: 25px;
        font-size: 12px;
        color: #929090;
    }
    .head-balance .bottom-fee{
        line-height: 25px;
        color: #f4733b;
        font-size: 24px;
    }
    .main_panel{
        padding: 20px;
    }
    .my-list{padding-top: 20px}
    .my-list li{
        height: 60px;
        line-height: 60px;
        margin-top: 10px;
        padding-left: 20px;
    }
    .my-list li a{
        display: flex;
        width: 100%;
        position: relative;
        text-align: left;
    }
    .my-list li.flex{
        display: flex;
        color: #929090;
    }
    .my-list li .title{
        flex:1;
        text-align: left;
    }
    .my-list li .count{
        flex:1;
        text-align: right;
        padding-right: 40px;
        color: #f4733b;
    }
    .my-list li i{
        position: absolute;
        top: 20px;
        right: 10px;
        padding: 10px;
        font-size: 12px;
        background: url('../assets/img/common/arrows.png') no-repeat;
        background-size: contain;
    }

    @media only screen and (max-height: 600px){
        .my-list{padding-top: 10px}
        .my-list li{
            height: 50px;
            line-height: 50px;
            margin-top: 10px;
            padding-left: 20px;
        }
    }
</style>



