<template>
<div>
    <page title="公告列表">
            <scroll @scrollToEnd="scrollToEnd" :data="listData" class="scroll comm_bg">
            <ul class="tw-list " ref="listbox">
                <li  v-for="(item,index) in listData" :key="index" @click="s(item)"> 
                       <div class="title">{{item.title}}</div>
                    <div class="content">
                        <span class="box">{{item.content}}</span>
                    </div>                         
                </li>
            </ul>
              <loading :imgshow="!noresults" :title="loadingtitle"></loading>
          </scroll>
    </page>
</div>
</template>

<script>
import page from "@/components/common/page";
import loading from "@/components/loading/loading";
import scroll from "@/components/common/scroll";
import { noticelist } from "../request/api";
export default {
  data() {
    return {
      listData: [],
      loading: true,
      noresults: false,
      loadingtitle: "正在加载",
      pagenum: 1
    };
  },
  components: {
    page,
    scroll,
    loading
  },
  created() {
    this.query({ cur: this.pagenum });
  },
  methods: {
    s(val){
        this.$router.push({ name: 'noticedetails', query: { id: val.id }})
    },
    query(param) {
      if (!typeof param == "object") {
        param = {};
      }
      noticelist(param).then(data => {
        let { msg, code, result } = data;
        if (code === 200) {
          if (result.page.list && result.page.list.length > 0) {
            if (result.page.list.length < 19) {
              this.noresults = true;
              this.loadingtitle = "没有更多了";
            }
          } else {
            this.noresults = true;
            this.loadingtitle = "没有更多了";
          }
          this.withdraw = result.page.withdraw;
          this.topup = result.page.topup;
          this.listData.push.apply(this.listData, result.page.list);
        } else {
          console.log(msg);
        }
      });
    },
    scrollToEnd() {
      if (this.loadingtitle !== "没有更多了") {
        this.query({ cur: ++this.pagenum });
      }
    }
  }
};
</script>

<style lang="stylus" scoped>
.scroll {
    height: 79vh;
    overflow hidden
    margin: 20px;
}

li {
    color: #346c91;
    padding: 10px;
    box-sizing: border-box;
    border-bottom: 1px solid #346c91;
    overflow: hidden;
}

.title {
    font-size: 16px;
    line-height:20px;
    text-align:left;
     overflow: hidden;
     height:38px;
     margin-bottom 10px;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    letter-spacing:2px;
}

.content {
    font-size: 12px;
    text-align: left;
    line-height: 18px;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    letter-spacing:2px;
}
</style>



