<template>
    <div class="main_panel">
        <div class="head-balance comm_bg">
            <div class="top-title">今日佣金收益</div>
            <div class="bottom-fee">
                {{(totalFee/100).toFixed(2)}}元
            </div>
        </div>


        <div class="mid_panel">
                      <scroll @scrollToEnd="scrollToEnd" :data="listData" class="scroll comm_bg">

            <ul class="tw-list " ref="listbox">
                <li class="topup" v-for="(item,index) in listData" :key="index">
                    <div class="left-box">
                        <div class="title">{{item.contribution_user_id ==0 ?'活动赠送':item.contribution_user_id}}</div>
                        <div class="time">{{item.create_time}}</div>
                    </div>
                    <div class="right-box plus"> +{{(item.fee/100).toFixed(2)}}元</div>
                </li>
            </ul>
                        <loading :imgshow="!noresults" :title="loadingtitle"></loading>
          </scroll>
        </div>
    </div>
</template>

<script>
import loading from "@/components/loading/loading";
import scroll from "@/components/common/scroll";
import { agencyTodayIncome } from "../request/api";
export default {
  name: "AgencyHistory",
  data() {
    return {
      listData: [],
      agencyName: 0,
      totalFee: 0,
      loading: true,
      noresults: false,
      loadingtitle: "正在加载",
      pagenum: 1
    };
  },
  components: {
    scroll,
    loading
  },
  created() {
    this.query({ cur: this.pagenum });
  },
  methods: {
    query(param) {
      if (!typeof param == "object") {
        param = {};
      }
      agencyTodayIncome(param).then(data => {
        let { msg, code, result } = data;
        if (code === 200) {
          if (result.page.list && result.page.list.length > 0) {
            if (result.page.list.length < 19) {
              this.noresults = true;
              this.loadingtitle = "没有更多了";
            }
          } else {
            this.noresults = true;
            this.loadingtitle = "没有更多了";
          }
          this.totalFee = result.page.totalFee;
          this.listData.push.apply(this.listData, result.page.list);
        } else {
          console.log(msg);
        }
      });
    },
    scrollToEnd() {
      if (this.loadingtitle !== "没有更多了") {
        this.query({ cur: ++this.pagenum });
      }
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.main_panel {
  padding: 20px;
}
.mid_panel {
  margin-top: 20px;
}
.head-balance {
  height: 70px;
}
.head-balance .top-title {
  padding-top: 6px;
  height: 25px;
  line-height: 25px;
  font-size: 12px;
  color: #929090;
}
.head-balance .bottom-fee {
  line-height: 25px;
  color: #f4733b;
  font-size: 24px;
}

.tw-list li {
  display: flex;
  height: 40px;
  padding: 5px 10px;
  border-bottom: solid 1px #f2f2f3;
}
.tw-list li .plus {
  color: #43ac3f;
}
.tw-list li .minus {
  color: #f4733b;
}

.tw-list li:last-child {
  border-bottom: 0;
}

.tw-list li .left-box {
  text-align: left;
  flex: 1;
}
.tw-list li .title {
  line-height: 24px;
  font-size: 12px;
}
.tw-list li .time {
  font-size: 12px;
  color: #7e8c8d;
}
.tw-list li .right-box {
  flex: 1;
  text-align: right;
  line-height: 40px;
}

.scroll {
  height: 75vh;
  overflow: hidden;
}

@media (device-height:568px) and (-webkit-min-device-pixel-ratio:2){/* 兼容iphone5 */
.scroll{
  height: 68vh;
  }
}

@media (device-height:667px) and (-webkit-min-device-pixel-ratio:2){/* 兼容iphone6 */
.scroll{
  height: 74vh;
  }
}
</style>



